/* ------------------------------------------------------------ *\
	Card Specs
\* ------------------------------------------------------------ */

.card-specs {
	position: relative;
	min-height: 72rem;
	box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

	&:before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		z-index: 2;
		height: 100%;
		background: linear-gradient(
			270deg,
			rgba(0, 0, 0, 1) 0%,
			rgba(0, 0, 0, 1) 35%,
			rgba(0, 0, 0, 0.2) 40%,
			rgba(0, 0, 0, 0) 100%
		);

		@include breakpoint-down(mobile) {
			display: none;
		}
	}

	&:nth-child(even):before {
		left: 0;
		background: linear-gradient(
			90deg,
			rgba(0, 0, 0, 1) 0%,
			rgba(0, 0, 0, 1) 35%,
			rgba(0, 0, 0, 0.2) 40%,
			rgba(0, 0, 0, 0) 100%
		);

		@include breakpoint-down(mobile) {
			display: none;
		}
	}

	@include breakpoint-down(mobile) {
		min-height: 43rem;
	}

	&:nth-child(even) .card__content {
		left: 0;
	}

	& + & {
		margin-top: 4rem;
	}

	&:nth-child(even) .card__background {
		right: 0;
		left: auto;
	}

	.card__background {
		position: absolute;
		top: 0;
		left: 0;
		width: 65%;
		height: 100%;
		z-index: 1;
		background-size: cover;
		background-position: center center;
		background-repeat: no-repeat;

		@include breakpoint-down(mobile) {
			min-height: 24rem;
			position: relative;
			width: 100%;
		}
	}

	.card__content {
		position: absolute;
		top: 50%;
		right: 0;
		transform: translateY(-50%);
		padding: 0 8rem;
		max-width: 72rem;
		width: 100%;
		z-index: 3;

		@include breakpoint-down(small-desktop) {
			max-width: 59rem;
		}

		@include breakpoint-down(mobile) {
			position: relative;
			background-color: $c-black;
			width: 100%;
			max-width: 100%;
			min-height: 100%;
			height: 100%;
			top: auto;
			left: auto;
			right: auto;
			bottom: auto;
			transform: translateY(0);
			padding: 4rem 2rem;
		}
	}

	.card__content h2 {
		margin-bottom: 2.4rem;

		@include breakpoint-down(mobile) {
			font-size: 3.2rem;
		}
	}
}
